
import { SaveLoadButtonStyle } from '../../config/autoPlanButtonStyle'
import './SaveLoad.css'
import UploadButton from './UploadButton'

const Save = ({courseState, progressionState}) => {

    const converttojson = () => {
        // console.log(progressionState.plannedPrograms);
        // console.log(progressionState.plannedPrograms());

        var data = {courseState:[], Programs:[], Specialisations:[]}
        courseState.completedCourses.forEach((course) => {
            data.courseState.push(course)
        })

        courseState.plannedCourses.forEach((course) => {
            data.courseState.push(course)
        })

        courseState.termCourseMap.forEach(element => {
           element.forEach((course) => {
            data.courseState.push(course)
           })
        });

        progressionState.plannedPrograms.forEach((Program) => {
            data.Programs.push(Program)
        })

        progressionState.plannedSpecialisations.forEach((Specialisation) => {
            data.Specialisations.push(Specialisation)
        })

        return data
    }

    const getcurrentdatetime =()=> {
        var currentdate = new Date(); 
        var datetime = "compsigh_" + currentdate.getDate() + "/"
                + (currentdate.getMonth()+1)  + "/" 
                + currentdate.getFullYear() + " @ "  
                + currentdate.getHours() + ":"  
                + currentdate.getMinutes() + ":" 
                + currentdate.getSeconds();

        return datetime
    }

    const handlesave =()=> {
        const data_save = converttojson()
        const filename = getcurrentdatetime();
        var FileSaver = require('file-saver');
        var blob = new Blob([JSON.stringify(data_save)], {type: "text/plain;charset=utf-8"});
        FileSaver.saveAs(blob, filename+".json");
    }
    return (
        <>
            <div className="savestyle">
                <button style={SaveLoadButtonStyle} onClick={handlesave}>
                    Save
                </button>
            </div>
            <div className="uploadstyle">
                <UploadButton courseState={courseState}  progressionState={progressionState}/>
            </div>
        </>
    )
}

export default Save
