import React, {useRef } from "react";
import { TermModel } from "../../models/TermModel";
import { SaveLoadButtonStyle} from "../../config/autoPlanButtonStyle";

const UploadButton = ({courseState, progressionState}) => {

    const inputRef = useRef(null);

    const handleFileUpload = (e) => {
        const file = e.target.files[0];
        let reader = new FileReader();
        courseState.setAllUnplanned();
        reader.onload = (e) => {
            const json = JSON.parse(e.target.result);
            json.courseState.forEach((course) => {
                if (course.courseStatus !== 2) {
                    setcompletedorPlanned(course);
                } else if (course.courseStatus === 2) {
                    assignCourtoTerm(course);
                }
            })

            json.Programs.forEach((Program) => {
                progressionState._programMapById.get(Program.code)?.setPlanned()
            })

            json.Specialisations.forEach((Specialisation) => {
                progressionState._specialisationMapById.get(Specialisation.code)?.setPlanned()
            })

        }
		reader.readAsText(file);
        e.target.value = null;

    };

    const setcompletedorPlanned = (course_upload) => {
        if (course_upload.courseStatus === 1) {
            courseState.getCourseById(course_upload.code)?.setPlanned()
        }
        if (course_upload.courseStatus === 3) {
            courseState.getCourseById(course_upload.code)?.setCompleted()
        }
    }

    const assignCourtoTerm = (course_upload) => {
        let uploaded = courseState.getCourseById(course_upload.code)
        if (uploaded) {
            courseState.assignCourseToTermHard(uploaded, new TermModel( course_upload.allocatedTerm.year, course_upload.allocatedTerm.name))
        }
        
    }
    
    return (
        <React.Fragment>
            <button 
                onClick={() => inputRef.current.click()}
                style={SaveLoadButtonStyle} >
                Upload
            </button>
            <input
                ref={inputRef}
                onChange={(e) => handleFileUpload(e)}
                type="file"
                style={{ display: "none" }}
                accept="application/JSON"
            />

        </React.Fragment>
    );


       


}


export default UploadButton